/*-----------------------------------------------
|   Pre-loader
-----------------------------------------------*/
#preloader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: $white;
	z-index: 1100;
}
.line-scale > div { background-color: $primary; }
